.r-color {
  position: relative;
}

.r-color__label {
  display: inline-block;
  font-size: 0.75rem;
  font-weight: 500;
  color: #37474F;
  margin-bottom: 0.25rem;
}

.r-color__toggle {
  display: block;
  background-color: transparent;
  border-width: 0;
  cursor: pointer;
  padding: 0;
  text-align: left;
  outline: 0;
  width: 100%;
}

.r-color__toggle__content {
  border-width: 1px;
  border-color: #C6CCD0;
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  height: 2.5rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  line-height: 1.25;
  background-color: var(--raul-select-bg, #fff);
  border-radius: var(--raul-select-border-radius, 0.125rem);
  color: var(--raul-select-color, #37474F);
}

.r-color__toggle__content__text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-grow: 1;
  font-size: 1rem;
}

@media (min-width: 1024px) {
  .r-color__toggle__content__text {
    font-size: 0.875rem;
  }
}

.r-color__toggle__content__indicator {
  margin-left: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 9999px;
}

.r-color__toggle:focus {
  outline: 0;
}

.r-color__toggle:focus .r-color__toggle__content {
  border-color: #0076CC;
}

.r-color__toggle:disabled .r-color__toggle__content {
  background-color: #EBEDEE;
  border-color: #EBEDEE;
  color: #9BA3A7;
  cursor: not-allowed;
}

.r-color__label {
  display: inline-block;
  font-size: 0.75rem;
  font-weight: 500;
  font-size: 0.75rem;
  margin-bottom: 0.25rem;
}

.r-color__error {
  display: flex;
  font-size: 0.75rem;
  align-items: center;
  color: #D01A1F;
  margin-top: 0.5rem;
}

.r-color__error__icon {
  font-size: 1rem;
  margin-right: 0.25rem;
}

.r-color__menu {
  flex-direction: row;
  border-width: 1px;
  border-radius: 0.125rem;
  border-color: #EBEDEE;
  box-shadow: 0 8px 16px 0 rgba(82, 97, 115, 0.18);
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  display: none;
  background-color: #fff;
  transition: opacity 0.2s ease, scale 0.2s linear;
  z-index: 100000;
}

.r-color__menu__content {
  display: flex;
  flex-direction: column;
  max-height: 20rem;
  width: 100%;
  min-height: 2.5rem;
}

@media (max-width: 640px) {
  .r-color__menu__content {
    max-height: none;
  }
}

.r-color__menu__body {
  display: flex;
  align-items: center;
  padding: 11px;
}

@media (max-width: 640px) {
  .r-color__menu__body {
    padding: 1rem;
    justify-content: space-between;
  }
}

.r-color__menu__body .r-color__menu__item {
  color: #fff;
  margin: 0.25rem;
  border-radius: 9999px;
  padding: 3px;
  border: 1px solid #fff;
}

.r-color__menu__body .r-color__menu__item:focus, body[modality=keyboard] .r-color__menu__body .r-color__menu__item:focus {
  border: 1px solid #0076CC;
  outline: none;
}

@media (max-width: 640px) {
  .r-color__menu {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-shadow: none;
    margin: 0;
    min-width: 0;
    position: fixed;
    border-width: 0;
    border-radius: 0;
    transform: scale(0, 0);
  }
}

.r-color__mobile-label {
  display: none;
}

@media (max-width: 640px) {
  .r-color__mobile-label {
    display: block;
  }

  .r-color__mobile-label__content {
    align-items: center;
    border-bottom-width: 1px;
    border-color: #EBEDEE;
    background-color: #F7F8F9;
    display: flex;
    font-size: 0.875rem;
    height: 2.5rem;
  }

  .r-color__mobile-label__content__text, .r-color__mobile-label__content__button {
    width: 33.333333%;
  }

  .r-color__mobile-label__content__text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #37474F;
    font-weight: 500;
    text-align: center;
  }

  .r-color__mobile-label__content__button {
    color: #0076CC;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    cursor: pointer;
  }
}

.r-color--opened .r-color__menu {
  display: flex;
}

.r-color--expanded .r-color__menu {
  opacity: 1;
}

@media (max-width: 640px) {
  .r-color--expanded .r-color__menu {
    transform: scale(1, 1);
  }
}

.r-color--invalid .r-color__toggle__content {
  border-color: #D01A1F;
}

raul-color-picker[variant=plugin] {
  display: block;
  position: relative;
}

raul-color-picker .pcr-app {
  display: none;
  position: absolute;
  left: 0 !important;
  top: 100% !important;
  transform: translateY(10px);
}

raul-color-picker .pcr-app.visible {
  display: flex;
}

raul-color-picker .pcr-app.visible .pcr-interaction .pcr-result {
  background-color: #fff;
  color: #37474F;
  margin-top: 0.25rem;
  border: 1px solid #EBEDEE;
}

raul-color-picker .pcr-app.visible .pcr-interaction .pcr-result:hover {
  -webkit-filter: none;
  filter: none;
}

raul-color-picker .pcr-app.visible .pcr-interaction .pcr-result:focus {
  border-color: #0076CC;
  outline: 0;
  box-shadow: none;
}

@media (min-width: 640px) {
  raul-color-picker .pcr-app.visible {
    font-family: "Roboto", sans-serif;
  }

  raul-color-picker .pcr-app.visible[data-theme=monolith] .pcr-selection .pcr-color-chooser {
    margin-top: 1rem;
  }

  raul-color-picker .pcr-app.visible[data-theme=monolith] .pcr-selection .pcr-color-opacity {
    margin-top: 0.5rem;
  }

  raul-color-picker .pcr-app.visible[data-theme=monolith] .pcr-color-palette .pcr-picker {
    width: 14px;
    height: 14px;
  }

  raul-color-picker .pcr-app.visible[data-theme=monolith] .pcr-color-chooser .pcr-picker,
raul-color-picker .pcr-app.visible[data-theme=monolith] .pcr-color-opacity .pcr-picker {
    width: 0.75rem;
    height: 0.75rem;
  }
}